.contacts-list {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  padding: 0;
  list-style: none;
  margin-top: 0;

  @include for-mobile-only {
    gap: 20px;
  }

  @include for-tablet-up {
    flex-wrap: nowrap;
  }
}

.contacts-item {
  background: var(--white-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  width: 280px;
  padding: 40px 32px;

  @include for-tablet-up {
    width: calc(33.3333333333% - 20px);
    height: 538px;
    padding: 40px 32px;
  }

  @include for-desktop-up {
    padding: 40px;
  }

  &__label {
    padding: 0px 25px;
    margin-bottom: 20px;
    background: var(--brown-color);
    border-radius: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    display: inline-block;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--white-color);

    &--green {
      background: var(--green-color);
    }

    @include for-tablet-up {
      font-size: 16px;
      line-height: 30px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__title {
    font-family: 'DM Sans', serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    letter-spacing: 0.04em;
    color: var(--black-color);

    @include for-tablet-up {
      font-size: 14px;
      line-height: 26px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__description {
    margin-top: 8px;
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: var(--grey-color);
    box-sizing: border-box;
    background: none;
    outline: none;
    resize: none;
    border: 0;
    border-bottom: 1px solid var(--grey-second-color);
    padding-bottom: 24px;

    @include for-tablet-up {
      font-size: 14px;
      line-height: 23px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 29px;
    }
  }

  &_devider {
    border: 1px solid #e1e1e1;
  }

  &__working-days {
    margin-top: 24px;
    margin-bottom: 0px;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: var(--black-color);

    &--last {
      margin-top: 8px;
    }

    @include for-tablet-up {
      font-size: 14px;
      line-height: 26px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__working-hours {
    margin: 0px;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: var(--pink-color);

    &--last {
      margin-bottom: 24px;
      box-sizing: border-box;
      background: none;
      outline: none;
      resize: none;
      border: 0;
      border-bottom: 1px solid var(--grey-second-color);
      padding-bottom: 24px;
    }

    @include for-tablet-up {
      font-size: 14px;
      line-height: 26px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__link {
    display: block;
    margin-top: 24px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: var(--grey-color);

    &--email {
      margin-top: 0px;
    }

    @include for-tablet-up {
      font-size: 14px;
      line-height: 26px;
    }

    @include for-desktop-up {
      font-size: 16px;
      line-height: 29px;
    }
  }
}
.contacts-button-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 32px;

  @include for-tablet-up {
    flex-direction: row;
    margin-top: 60px;
    margin-right: 15px;
  }
}

.contacts__btn {
  width: 162px;
  height: 44px;
  line-height: 44px;
  justify-content: center;
  padding: 0;
  gap: 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  cursor: pointer;

  &-icon {
    margin-top: 2px;
    stroke: var(--white-color);
    &--red {
      margin-top: 2px;
      stroke: var(--red-color);
    }
  }
}
